
import VHeader from '@/components/v-header/v-header'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import axios from '@/api/axiosApi'
import nativeBridge from '@/assets/js/nativeBridge'

export default {
data () {
return {
styles: {
height: '100%'
},
menusPhone: {
menu1: '咨询拨打官方电话 8888888'
},
orderId: '',
orderMessage: {
orderStatusDesc: ''
},
showPhone: false,
showQuit: false,
invoiceMessage: {},

outTradeNoQuit: '',
imgUrl: ''
}
},
created () {
this.imgUrl = 'http://p2pblz7ar.bkt.clouddn.com/orderback.png'
this.getOrderDesc()
this.getInvoiceDesc()
},
methods: {

getOrderDesc () {
axios.get(this, '/v1/order/' + this.$route.params.descId, null, (data) => {
switch (data.orderStatus) {
case 0:
data.orderStatusDesc = '待付款'
break
case 1:
data.orderStatusDesc = '已付款'
break
case 2:
data.orderStatusDesc = '已取消'
break
case 3:
data.orderStatusDesc = '退款中'
break
case 4:
data.orderStatusDesc = '已退款'
break
case 5:
data.orderStatusDesc = '退款失败'
break
}

if (this.moment().subtract(1, 'days').isAfter(data.effectiveDate.split('~')[1]) || this.moment().subtract(1, 'days').isAfter(data.effectiveDate)) {
data.orderStatusDesc = '已失效'
data.expire = true
}
if (typeof data.notice !== 'undefined') {
data.notice = data.notice.replace(/；/g, ';').split(';')
}
this.orderMessage = data
console.log(this.orderMessage)
})
},

getInvoiceDesc () {
axios.get(this, '/v1/invoice/' + this.$route.params.descId, null, (data) => {
this.invoiceMessage = data
})
},
handleToProductDesc (id, type) {
this.$router.push('/address/product/' + id + '/' + type)
},

handleConnect (outTradeNo) {
this.outTradeNoQuit = outTradeNo
this.showQuit = true
},

handleConnectPhone() {
this.showPhone = true
},
handleClickMenu (value) {
window.location.href = 'tel://8888888'
},
toPay (outTradeNo) {
nativeBridge.alipay(outTradeNo, ({arg, msg}) => {
if (arg === 9000) {
this.$store.commit('SET_ORDER_TAB', 2)
this.$router.push('/user/order')
}
this.$vux.toast.text(msg, 'middle')
})
},
toCansel (outTradeNo) {
axios.put(this, '/v1/order', {outTradeNo: outTradeNo, orderStatus: 2}, (data) => {
this.$vux.toast.text('订单取消成功', 'middle')
this.$router.push('/user/order')
this.$store.commit('SET_ORDER_TAB', 3)





})
},
onCancelQuit () {},
onConfirmQuit () {
axios.post(this, '/v1/refund/' + this.outTradeNoQuit, null, (data) => {
this.$router.push('/user/orderQuitServer')
this.$store.commit('SET_QUITORDER_TAB', 0)
})
}
},
components: {
VHeader,
Group,
Cell,
CellBox,
Flexbox,
FlexboxItem,
Card,
Flow,
FlowState,
FlowLine,
Scroller,
XButton,
Actionsheet,
Divider,
Confirm
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
}
}



